import {useState, useEffect} from 'react';

const useMultiSelect = (initialItems, initialSelectedItemsIndices) =>{
  const [items, setItems] = useState(initialItems || []);
  const [selectedItemsIndices, setSelectedItemsIndices] = useState(initialSelectedItemsIndices || []);


  //Create helper functions
  const isSelected = id=>{
    return selectedItemsIndices.includes(id);
  };

  const isAllSelected = ()=>{
    return selectedItemsIndices.toString() === items.map((e,i)=>i).toString();
  };

  const selectItem = id=>{
    return setSelectedItemsIndices(previousSelectedItemsIndices=>[
      ...previousSelectedItemsIndices,
      id,
    ]);
  };

  const deselectItem = id=>{
    return setSelectedItemsIndices(previousSelectedItemsIndices=>{
      const index = previousSelectedItemsIndices.indexOf(id);
      if(index === -1) return previousSelectedItemsIndices;
      return [
        ...previousSelectedItemsIndices.slice(0,index),
        ...previousSelectedItemsIndices.slice(index+1),
      ];
    });
  };

  const selectAllItems = ()=>{
    return setSelectedItemsIndices(items.map((e,i)=>i));
  };

  const deselectAllItems = ()=>{
    return setSelectedItemsIndices([]);
  };


  //Create exported functions
  const toggleItem = id=>{
    return (isSelected(id) ? deselectItem(id) : selectItem(id));
  };

  const toggleAllItems = ()=>{
    return (isAllSelected() ? deselectAllItems() : selectAllItems());
  };


  const itemsWithSelection = items.map((e,i)=>({
    id: i,
    item: e,
    selected: isSelected(i),
  }));

  //When the passed items change,
  // update the items state
  useEffect(()=>{
    if(!initialItems) return;
    setItems(initialItems);
  }, [initialItems]);

  //When the passed selectedItemIndices change,
  // update the selectedItemIndices state
  useEffect(()=>{
    if(!initialSelectedItemsIndices?.length) return;
    setSelectedItemsIndices(initialSelectedItemsIndices);
  }, [initialSelectedItemsIndices]);


  return {itemsWithSelection, toggleItem, toggleAllItems};
};

export default useMultiSelect;
